export function Whoops404({resource}) {
  let location = useLocation();

  if (!resource) {
     return (
        <div>
               <h1>404: Resource not found at '{location.pathname}'!</h1>
        </div>
     );
  }

  return (
    <div>
           <h1>404: Resource '{resource}' not found!</h1>
    </div>
  );
}

